<template>
  <div class="wrapper">
    <section class="wrapper-box">
      <h1>榜单</h1>
      <ul class="list-type" flex>
        <li class="item" v-for="(item, index) in ranks" :key="index" @click="toSongRank(index)">{{ item }}</li>
      </ul>
    </section>
    <section class="wrapper-box">
      <h1>音乐类型</h1>
      <ul class="list-type" flex>
        <li class="item" v-for="(item, index) in types" :key="index" @click="toSongList('type', item)">{{ item }}</li>
      </ul>
    </section>
    <section class="wrapper-box">
      <h1>音乐语言</h1>
      <ul class="list-type" flex>
        <li class="item" v-for="(item, index) in languages" :key="index" @click="toSongList('language', item)">{{ item }}</li>
      </ul>
    </section>
    <section class="wrapper-box">
      <h1>歌手</h1>
      <ul class="list-type" flex>
        <li class="item" v-for="(item, index) in singers" :key="index" @click="toSongList('singer', item.singer)">{{ item.singer }}</li>
      </ul>
    </section>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      ranks: ['新歌榜', '热歌榜', '热搜榜', '高分榜'],
      types: [],
      languages: [],
      singers: []
    }
  },
  created () {
    this.getType()
    this.getLanguage()
    this.getSinger()
  },
  methods: {
    // 获取音乐类型
    async getType () {
      const res = await this.$http.post('/index/data/getSongType', {})
      this.types = res.data.types
    },
    // 获取音乐语言
    async getLanguage () {
      const res = await this.$http.post('/index/data/getSongLanguage', {})
      this.languages = res.data.languages
    },
    // 获取歌手
    async getSinger () {
      const res = await this.$http.post('/index/data/getSinger', {})
      this.singers = res.data
    },
    // 前往榜单列表页
    toSongRank (index) {
      this.$router.push({
        path: '/song/rank',
        query: {
          index: index
        }
      })
    },
    // 前往歌曲列表页
    toSongList (type, item) {
      this.$router.push({
        path: '/song/detail',
        query: {
          type: type,
          item: item
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .wrapper {
    margin: 30px 0 0;
    height: 100%;
    overflow: hidden;
    overflow-y: auto;
    .wrapper-box {
      h1 {
        padding: 0 0 0 10px;
        height: 30px;
        line-height: 30px;
        font-size: 20px;
        letter-spacing: 1px;
        color: #efefef;
        border-left: 4px solid #b2b2b2;
      }
      .list-type {
        flex-wrap: wrap;
        .item {
          margin: 16px 10px 16px 15px;
          padding: 5px 10px;
          height: 20px;
          line-height: 20px;
          font-size: 16px;
          color: #efefef;
          border-bottom: 1px solid transparent;
          cursor: pointer;
          &:first-child {
            padding-left: 0px;
          }
          &:hover {
            color: #fff;
            border-bottom: 1px solid #fff;
          }
        }
      }
    }
  }
</style>
